@import (inline) '../../bower_components/normalize.css/normalize.css';
@import "elements";
*{box-sizing: border-box;}
body{
  font-family: 'quicksandregular', arial, sans-serif;
}
ul,h2{
  margin:0;
}
.Header{
  height:70px;

  position: relative;
  .Logo{
    background: transparent url(/assets/logo.png) center left no-repeat;
    background-size: contain;
    width:400px;
    height:70px;
    text-indent: -10000px;
    margin-left:24px;
  }
  .Nav{
    position: absolute;
    top:0;
    right:70px;
    .Item{
      display:inline-block;
      height:70px;
      line-height: 70px;
      vertical-align: middle;
      font-size: 24px;
      padding: 0 12px;

      a:link,a:hover,a:visited{
        color:black;
        text-decoration: none;

        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
}
.Hero{
  background: transparent url(/assets/hero.jpg) no-repeat;
  background-size: cover;
  height:435px;
  position: relative;

  .ComingSoon{
    background:transparent url(/assets/coming-soon.svg) center no-repeat;
    background-size: contain;
    width:40%;
    height:300px;
    text-indent: -10000px;
    position: absolute;
    top:20px;
    left:50px;
  }

  .Callout{
    background-color: rgba(0,0,0,0.47);
    border-radius: 20px;
    position: absolute;
    width: 500px;
    height:250px;
    bottom: 40px;
    right:80px;
    color:white;
    text-align: center;
    font-size: 26px;
    padding:40px;

    .Button{
      display: inline-block;
      height:40px;
      width:150px;
      background-color: yellow;
      border-radius: 5px;
      border: 2px solid white;
      margin: 10px;
      font-size: 22px;
      line-height: 36px;
      .gradient(rgb(232,198,59),rgb(185,153,18),rgb(240,205,65));

      &:link,&:hover,&:visited{
        color:black;
        text-decoration: none;
      }
    }
  }
}
.Section{
  margin: 50px;
  padding: 40px;
  height:450px;
  color:white;
  position: relative;

  &.type-code{
    background-color: #DC382B;
  }
  &.type-web{
    background-color: #5CA3DA;
  }
  &.type-app{
    background-color: #F0CF42;
  }
  &.type-mods{
    background-color: #215A31;
  }
  h2{
    font-size: 36px;
  }
  .description{
    width:230px;
    font-size: 24px;
    float:right;
  }
}
.Display{
  position: absolute;
  bottom:0;
  left:0;
  right:280px;
  top:100px;
  overflow: hidden;

  .DisplayImage{
    background-size: contain;
    background-position: top left;
    background-repeat: no-repeat;
    position: absolute;
    .drop-shadow(5px,5px,4px,0.4);

    &.lightbot{
      background-image: url(/assets/lightbot.jpg);
      width:400px;
      height:301px;
      .rotation(-20deg);
      bottom: -35px;
      left:-30px;
      z-index: 10;
    }
    &.robotic-friend{
      background-image: url(/assets/roboticfriends.jpg);
      width:400px;
      height:287px;
      .rotation(-15deg);
      bottom: -35px;
      left:15%;
      z-index: 5;
    }
    &.code-combat{
      background-image: url(/assets/codecombat.jpg);
      width:400px;
      height:319px;
      .rotation(-10deg);
      bottom: -65px;
      left:35%;
    }
    &.atom{
      background-image: url(/assets/atom.png);
      width:800px;
      height:363px;
      .rotation(-3deg);
      bottom: -35px;
      left:0px;
    }
    &.bitsbox{
      background-image: url(/assets/bitsbox.jpg);
      width:500px;
      height:385px;
      .rotation(-5deg);
      bottom: -100px;
      left:0;
      z-index: 10;
    }
    &.scratch{
      background-image: url(/assets/scratch.jpg);
      width:500px;
      height:385px;
      .rotation(-2deg);
      bottom: -100px;
      right:30px;

    }
    &.minecraft{
      background-image: url(/assets/minecraft.jpeg);
      width:600px;
      height:338px;
      .rotation(-3deg);
      bottom:-30px;
      left:40px;
    }
  }
}

